body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
    width:200px;
    height:200px;
    margin:15px;
    box-shadow:0px 0px 5px black;
    
}
img:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.score {
    padding-top:20px;
    padding-bottom:20px;
    background: #fcfbe5 #ffe;
    color:#fcfbe5 #ffe;
    font-size:25px;
}
.game-area{
    margin-top:10px;
    padding-bottom:20px;
}

.game-heading{
    padding-top:50px;
    padding-bottom:50px;
    background:;
    margin-bottom:10px;
}
.score, .game-heading{
    font-family: 'Courgette', cursive;
}
