.scoreboard{
    font-size:30px;
}

p{
    color:#41200f#421;
    font-size:45px;
}

.container{
    background:url("https://www.youredm.com/wp-content/uploads/2017/07/Game-of-Thrones-Logo.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}
span{ 
    background: url("https://d9np3dj86nsu2.cloudfront.net/image/f7347bbd9323fc01ee80d11c76ab6270") -505px -505px repeat;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    display:block;
    font-size:25px;
    font-weight:bold;
    font-family: 'Permanent Marker', cursive;
}
span.game-name { 
    background: url("https://d9np3dj86nsu2.cloudfront.net/image/f7347bbd9323fc01ee80d11c76ab6270") -505px -505px repeat;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    display:block;
    font-size:70px;
    font-weight:bold;
    font-family: 'Permanent Marker', cursive;
}

@media screen and (max-width: 1199px){
    img {
        width:200px;
        height:200px;
        margin:25px;
    }
}

@media screen and (max-width: 991px){
    img {
        width:230px;
        height:230px;
        margin:25px;
    }
}

@media screen and (max-width: 767px) {
    img {
        width:200px;
        height:200px;
        margin:25px;
    }
}

@media screen and (max-width: 575px) {
    img {
        width:400px;
        height:400px;
    }
    .score{
        width:100vw;
    }
}
@media screen and (max-width: 475px) {
    img {
        width:300px;
        height:300px;
    }
    .score{
        width:100vw;
    }
}
